.noWebGL {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 1;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
}

.noWebGL span, .noWebGL a {
    font-size: 14px;
    display: inline;
}

.noWebGL a {
    text-decoration: underline;
}